<script setup>
import { onMounted, ref, nextTick, computed, onUnmounted } from 'vue'
import {
  GetStatPeriod,
  GetgGtLast,
  GetMotionCrowdProjectRatio,
  GetNowMonthStatByGroup,
  GetWaterRecordList,
  GetStatWaterRecord,
  GetStatPassenger,
  GetMotionData,
  GetNowMonthStat,
  GetRemainSpaceNum,
  GetDevsInfo
} from '../api/home'
import p1 from '@/assets/images/ic_park1@2x.png'
import p2 from '@/assets/images/ic_park2@2x.png'
import p3 from '@/assets/images/ic_park3@2x.png'

import * as echarts from 'echarts'
// 获取当前日期
const currentDate = ref(new Date())
// 格式化日期为 "YYYY年MM月DD日 周X"
const formattedDate = computed(() => {
  const year = currentDate.value.getFullYear()
  const month = String(currentDate.value.getMonth() + 1).padStart(2, '0') // 月份从0开始，所以要+1
  const day = String(currentDate.value.getDate()).padStart(2, '0')
  const weekDays = ['周日', '周一', '周二', '周三', '周四', '周五', '周六']
  const dayOfWeek = weekDays[currentDate.value.getDay()]

  return `${year}年${month}月${day}日 ${dayOfWeek}`
})
// 创建一个响应式变量来存储当前时间
const currentTimeRef = ref(
  new Date().toLocaleTimeString('zh-CN', { hour: '2-digit', minute: '2-digit' })
)
const currentTime = computed(() => currentTimeRef.value)
// 定时器ID，用于在组件卸载时清除定时器
let timerId = null
// 更新当前时间的函数
function updateCurrentTime() {
  currentTimeRef.value = new Date().toLocaleTimeString('zh-CN', {
    hour: '2-digit',
    minute: '2-digit'
  })
}
// 停车场数据变量
const pImg = [p1, p2, p3]
const parkingLotData = ref({})
const parkingLotNum = ref(null)
let parkingLotDataTotal = ref(null)
// 获取停车场数据
const getParkingLotData = async (type = 'right') => {
  // const { code, data } = await GetRemainSpaceNum()
  // if (code != 200) return
  let data = {
    "2号停车场": {
      "name": "2号停车场",
      "remainNum": 200,
      "useName": 100,
      "total": 300
    },
    "3号停车场": {
      "name": "3号停车场",
      "remainNum": 100,
      "useName": 400,
      "total": 500
    },
    "1号停车场": {
      "name": "1号停车场",
      "remainNum": 100,
      "useName": 400,
      "total": 500
    }
  }
  const newparkingLotData = Object.keys(data)
    .sort((a, b) => {
      const numA = parseInt(a.match(/\d+/)[0], 10)
      const numB = parseInt(b.match(/\d+/)[0], 10)
      return numA - numB
    })
    .map(key => data[key])
  parkingLotDataTotal.value = newparkingLotData.length
  if (type == 'right') {
    if (parkingLotNum.value >= newparkingLotData.length) {
      parkingLotNum.value = 1
    } else {
      parkingLotNum.value += 1
    }
  } else {
    if (parkingLotNum.value <= 1) {
      parkingLotNum.value = newparkingLotData.length
    } else {
      parkingLotNum.value -= 1
    }
  }

  parkingLotData.value = newparkingLotData[parkingLotNum.value - 1]
}
// 计算百分比
const calculatePercentage = (num, total) => {
  if (!total) return '-'
  return Math.round((num / total) * 100) + '%'
}

