const venueEnvironment = ref({}) // 当前数据
const venueEnvironActNum = ref(null) // 当前场馆
const venueEnvironTotal = ref(null) // 场馆总数
const getVenueEnviron = async (type = 'right') => {
  const { code, data } = await GetgGtLast()
  if (code != 200) return
  venueEnvironment.value = data
  const venueEnvironKeys = Object.keys(data)
  const venueEnvironVals = Object.values(data)
  venueEnvironTotal.value = venueEnvironKeys.length
  const venueEnvironmentData = venueEnvironVals.map((item, index) => {
    return { ...item, name: venueEnvironKeys[index] }
  })
  if (type == 'right') {
    if (venueEnvironActNum.value >= venueEnvironTotal.value) {
      venueEnvironActNum.value = 1
    } else {
      venueEnvironActNum.value += 1
    }
  } else {
    if (venueEnvironActNum.value == 1) {
      venueEnvironActNum.value = venueEnvironTotal.value
    } else {
      venueEnvironActNum.value -= 1
    }
  }
  venueEnvironment.value = venueEnvironmentData[venueEnvironActNum.value - 1]
}
getVenueEnviron() // 场馆环境数据

