// 开始滚动的方法
const startScroll = () => {
  clearAnimationFrame()
  const step = () => {
    if (!isHovered.value && !flagScor.value) {
      // 非悬停状态时执行滚动
      scrollTop.value += 0.2 // 每帧滚动距离
      // 当滚动到第一组末尾时重置位置
      const maxScroll = itemHeight * alarmInformation.value.length
      if (scrollTop.value >= maxScroll * 2) {
        scrollTop.value -= maxScroll + 10
      }
    }
    // 请求下一帧动画
    animationFrame.value = requestAnimationFrame(step)
  }
  // 启动动画循环
  animationFrame.value = requestAnimationFrame(step)
}
// 鼠标移入移出事件  添加类名
