}
// 移出
const delactWar = () => {
  if (flagScor.value) return
  isHovered.value = false
  // indexVal.value = null
}

// 计算时间差的函数
function calculateTimeDifference(targetTime) {
  const targetDate = new Date(targetTime)
  const currentDate = new Date()

  // 计算时间差的毫秒数
  const timeDiff = currentDate - targetDate

  if (timeDiff > 0) {
    // 目标时间已过去
    const diffInSeconds = Math.floor(timeDiff / 1000) // 转换为秒

    if (diffInSeconds < 86400) {
      // 小于一天
      const hours = Math.floor(diffInSeconds / 3600) // 小时
      const minutes = Math.floor((diffInSeconds % 3600) / 60) // 分钟
      const seconds = diffInSeconds % 60 // 秒

      const parts = []
      if (hours > 0) parts.push(`${hours}小时`)
      if (minutes > 0) parts.push(`${minutes}分钟`)
      if (seconds > 0) parts.push(`${seconds}秒`)

      return `已发生${parts.join('')}`
    } else {
      // 超过一天
      const days = Math.floor(diffInSeconds / 86400) // 天数
      const remainingSeconds = diffInSeconds % 86400 // 剩余秒数
      const hours = Math.floor(remainingSeconds / 3600) // 小时
      const minutes = Math.floor((remainingSeconds % 3600) / 60) // 分钟

      const parts = []
      if (days > 0) parts.push(`${days}天`)
      if (hours > 0) parts.push(`${hours}小时`)
      if (hours == 0 && minutes > 0) parts.push(`${minutes}分钟`) // 仅在小时为0时显示分钟

      return `已发生${parts.join('')}`
    }
  } else {
    return '尚未过期' // 如果目标时间尚未过去
  }
}

// 计算所有时间差并设置到timeDifferences中
function updateTimeDifferences() {
  timeDifferences.value = targetTimes.value.map(timeStr => {
    // 在这里，timeStr 是从 targetTimes 数组中自动传递的每个时间字符串
    return calculateTimeDifference(timeStr)
  })
}
// 组件挂载时设置定时器
const getAllData = () => {
  getParkingLotData() // 获取停车数据
  getEnergyConsumpData() // 获取能耗数据
  getZDEnergyConsumpEcharts() //本月能耗图表数据-ZD
  getPassengerTraffic() //  统计客人流量数据
  changeZDPassengerTraffic() // 客流量分析图表数据--ZD
  // getEnergyConsumpEcharts() // 获取本月能耗echarts数据
  getZDPlayEchartsData() // 获取运动图表数据-ZD
  getSportsData() // 运动数据分析三要素
  getVenueEnviron() // 场馆环境数据
  getSecurityAndDrowning() // 水域安防和放溺水数据
  getDevsData() //获取告警信息
}
let timeData = null
timeData = setInterval(getAllData, 1000 * 60) // 一分钟请求接口

let twoMinTime = null
const twoMinutesData = () => {
  getWaterRecordData() // 获取告警信息
}
twoMinTime = setInterval(twoMinutesData, 1000 * 120)

let intervalFn = null //
onMounted(async () => {
  timerId = setInterval(updateCurrentTime, 1000) // 每秒更新一次
  getEnergyConsumpData() // 获取能耗数据
  getPassengerTraffic() //  统计客人流量数据
  changePassengerTraffic() // 客流量分析图表数据
  getEnergyConsumpEcharts() // 获取本月能耗echarts数据
  getPlayEchartsData() // 获取运动图表数据
  getParkingLotData() // 停车场数据
  // initEchartSenergyConsump() // 本月能耗图表
  // initTrafficEcharts() //客流量分析图表
  // initPlayEcharts() // 运动
  // startScrolling()
  // initEchartequiSitua() // 设备情况each
  getDevsData() // 获取设备情况数据
  await getWaterRecordData() // 警告信息

  // 每分钟更新一次所有时间差
  updateTimeDifferences()
  // await startScroll()
  intervalFn = setInterval(updateTimeDifferences, 1000)
})

// 清除报警图片滚动
const clearAnimationFrame = () => {
  if (animationFrame.value) {
    cancelAnimationFrame(animationFrame.value)
    animationFrame.value = null
  }
}
// 组件卸载时清除定时器
onUnmounted(() => {
  clearInterval(timerId)
  clearInterval(intervalFn) // 过期点位
  clearInterval(timeData) // 总数据请求
  clearInterval(twoMinTime)
  clearAnimationFrame()
})
